body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  align-items: center;
  min-width: 1024px;
  max-width: 1200px;
  padding: 0 100px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo_link {
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav {
  flex: 1 0 auto;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__list {
  height: 15px;
  font-size: 12px;
  font-weight: bold;
  display: flex;
}

.nav__item {
  justify-items: baseline;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: baseline;
  margin-left: 20px;
  text-decoration: none;
  display: flex;
}

.nav__link:link {
  color: #000;
  text-decoration: none;
}

.nav__link:visited {
  color: #00acdc;
}

.nav__link:hover {
  text-decoration: underline #00acdc;
}

.nav__link:active {
  border-bottom: 4px solid #00acdc;
  border-radius: 5%;
  padding-bottom: 25px;
  text-decoration: underline #00acdc;
}

.nav__link.active:after {
  content: "";
  background-color: #007bff;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -3px;
  left: 0;
}
