body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  display: flex;
  background-color: #fff;
  align-items: center;
  padding: 0 100px;
  max-width: 1200px;
  min-width: 1024px;
}

.logo {
  display: block;
  height: 40px;
  width: 40px;
}

.logo_link {
  display: flex; /* Чтобы сделать ссылку такого же размера, как изображение */
  width: 40px;
  height: 40px;
  box-sizing: border-box;
}

.nav {
  display: flex;
  justify-content: space-between;
  flex: auto;
  align-items: center;
  height: 60px;
  flex-shrink: 0;

}

.nav__list {
  display: flex;
  font-size: 12px;
  font-weight: bold;
  height: 15px;
}

.nav__item {
  display: flex;
  justify-items: baseline;
}

.nav__link {
  color: #000;
  display: flex;
  align-items: baseline;
  margin-left: 20px;
  text-decoration: none;
}

.nav__link:link {
  color: #000;
  text-decoration: none;
}

.nav__link:visited {
  color: #00acdc;
}

.nav__link:hover {
  text-decoration: underline;
  text-decoration-color: #00acdc;
}

.nav__link:active {
  text-decoration: underline;
  text-decoration-color: #00acdc;
  border-bottom: 4px solid #00acdc; /* Добавляем линию снизу */
  border-radius: 5%;
  padding-bottom: 25px;
}

.nav__link.active::after {
  content: ''; /* Псевдоэлемент для линии */
  position: absolute;
  left: 0;
  bottom: -3px; /* Расположение под текстом */
  width: 100%;
  height: 2px; /* Толщина линии */
  background-color: #007bff;
}
